<template>
    <div class="detail">
        <div class="detail-container">
            <div class="detail-title">
                <a href="#">{{dataObj.title}}</a>
            </div>
            <div class="detail-info">
                作者：rebecca 标签{{dataObj.tags}} 发布于{{dataObj.ctime}} 浏览{{dataObj.views}}
            </div>
            <div class="detail-content" v-html="dataObj.content">
                <!-- {{dataObj.content}} -->
            </div>
        </div>
         <leave-message :bid="bid" 
                        :rand-num="randNum" 
                        :now-page="nowPage"
                        @change:parent="changeParent" 
                        @totalCount="totalCount"/>

        <turn-page  :total-page="totalPage" 
                    :cur-page="nowPage" 
                    @turn-page="turnPage"/>

        <add-comment  :bid="bid" 
                      :parent="parent" 
                      :parent-name="parentName"
                      @reload="reloadPage" />
    </div>
    
</template>

<script>
import leaveMessage from "@/components/about/leaveMessage.vue"
import addComment from "@/components/about/addComment"
import turnPage from "@/components/index/turnPage.vue"
import { randomStr } from '@/util/random.js'
import { getDate } from '@/util/getDate.js'
export default{
    components:{
        addComment,
        leaveMessage,
        turnPage
    },
    data(){
        return{
            dataObj:{
                title:'',
                content:'',
                tags:'',
                ctime:'',
                views:'',
                id:'',
                utime:''
            },
            bid:-1,
            parent:-1,
            parentName:'',
            randNum:'',
            totalPage:10,
            nowPage:1,
            pageSize:2
        }
    },
    methods:{
        getBlogContent(bid){
            this.$axios.get('/queryBlogById',{
                params:{
                    bid
                }
            }).then(res => {
                const result = res.data.data[0];
                for(let prop in result){
                    this.dataObj[prop] = result[prop];
                    this.dataObj['ctime'] = getDate(result['ctime']);
                }
            }).catch(err => console.log(err))
        },
        changeParent(data){
            location.href = '#send';
            this.bid = data.blogId;
            this.parentName = data.parent;
        },
        reloadPage(){
            location.href = '';
            this.parentName = '';
            this.randNum = randomStr(20); 
        },
        computedBid(){
            let searchUrlParams = location.search.indexOf('?') > -1 ? location.search.split('?')[1]:"";
            if(searchUrlParams == ''){
                return;
            }
            let bid = -1;
            searchUrlParams = searchUrlParams.replace(/#[wW]*/g,'');
            if(searchUrlParams.split('=')[0] == 'bid'){
                try{
                    bid = searchUrlParams.split('=')[1];
                }catch(e){
                    console.log(e);
                }
            }
            this.getBlogContent(bid);
            this.bid = bid;
        },
        turnPage(i){
            this.nowPage = i;
        },
        totalCount(val){
            this.totalPage = Math.ceil(val/this.pageSize);
        }
    },
    created(){
        this.computedBid();
    }
}
</script>

<style lang="scss">
    @import "@/assets/css/detail/detail.scss"
</style>